<script>
import Submenu from '../components/Submenu.vue'

export default {
  data () {
    return {
      searchImg: []
    }
  },
  components: {
    Submenu
  },
  methods: {
    transmit (photos) {
      this.searchImg = photos.item
    }
  }
}
</script>

